				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('lajiyunshucheliang','首页总数')">
				<el-card v-show="countTypeList.closelajiyunshucheliangCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								垃圾运输车辆
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenlajiyunshucheliangCountType')" class="showIcons"
									:class="countTypeList.hiddenlajiyunshucheliangCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closelajiyunshucheliangCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenlajiyunshucheliangCountType">
							<div class="count_title">垃圾运输车辆总数</div>
							<div class="count_num">{{lajiyunshucheliangCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
		<div class="card_list">
			<el-collapse-transition v-if="btnAuth('lajitoufangdian','首页统计')">
				<el-card v-show="cardTypeList.closelajitoufangdianChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								垃圾投放点
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenlajitoufangdianChartType1')" class="showIcons"
									:class="cardTypeList.hiddenlajitoufangdianChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closelajitoufangdianChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenlajitoufangdianChartType1">
							<div id="lajitoufangdiantoufangdianfenleiEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('lajitoufangdian','首页统计')">
				<el-card v-show="cardTypeList.closelajitoufangdianChartType2" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								垃圾投放点
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenlajitoufangdianChartType2')" class="showIcons"
									:class="cardTypeList.hiddenlajitoufangdianChartType2?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closelajitoufangdianChartType2')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenlajitoufangdianChartType2">
							<div id="lajitoufangdianrongliangEchart2" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('lajiyunshucheliang','首页统计')">
				<el-card v-show="cardTypeList.closelajiyunshucheliangChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								垃圾运输车辆
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenlajiyunshucheliangChartType1')" class="showIcons"
									:class="cardTypeList.hiddenlajiyunshucheliangChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closelajiyunshucheliangChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenlajiyunshucheliangChartType1">
							<div id="lajiyunshucheliangcheliangleixingEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
	</div>
</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closelajitoufangdianCountType = true
		countTypeList.value.hiddenlajitoufangdianCountType = true
		if(btnAuth('lajitoufangdian','首页总数')){
			getlajitoufangdianCount()
		}
		countTypeList.value.closerenyuanxinxiCountType = true
		countTypeList.value.hiddenrenyuanxinxiCountType = true
		if(btnAuth('renyuanxinxi','首页总数')){
			getrenyuanxinxiCount()
		}
		countTypeList.value.closelajiyunshucheliangCountType = true
		countTypeList.value.hiddenlajiyunshucheliangCountType = true
		if(btnAuth('lajiyunshucheliang','首页总数')){
			getlajiyunshucheliangCount()
		}
	}
	const lajitoufangdianCount = ref(0)
	const getlajitoufangdianCount = () => {
		context?.$http({
			url:'lajitoufangdian/count',
			method: 'get'
		}).then(res=>{
			lajitoufangdianCount.value = res.data.data
		})
	}
	const renyuanxinxiCount = ref(0)
	const getrenyuanxinxiCount = () => {
		context?.$http({
			url:'renyuanxinxi/count',
			method: 'get'
		}).then(res=>{
			renyuanxinxiCount.value = res.data.data
		})
	}
	const lajiyunshucheliangCount = ref(0)
	const getlajiyunshucheliangCount = () => {
		context?.$http({
			url:'lajiyunshucheliang/count',
			method: 'get'
		}).then(res=>{
			lajiyunshucheliangCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closelajitoufangdianChartType1: true,
		hiddenlajitoufangdianChartType1: true,
		closelajitoufangdianChartType2: true,
		hiddenlajitoufangdianChartType2: true,
		closelajiyunshucheliangChartType1: true,
		hiddenlajiyunshucheliangChartType1: true,
	})
	const getCardList = () => {
		if(btnAuth('lajitoufangdian','首页统计')){
			getlajitoufangdianChart1()
		}
		if(btnAuth('lajitoufangdian','首页统计')){
			getlajitoufangdianChart2()
		}
		if(btnAuth('lajiyunshucheliang','首页统计')){
			getlajiyunshucheliangChart1()
		}
	}
	const getlajitoufangdianChart1 = () => {
		nextTick(()=>{
			var toufangdianfenleiEchart1 = echarts.init(document.getElementById("lajitoufangdiantoufangdianfenleiEchart1"),'macarons');
			context?.$http({
				url: "lajitoufangdian/group/toufangdianfenlei",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].toufangdianfenlei);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].toufangdianfenlei
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '投放点类型占比统计',
                        left: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				toufangdianfenleiEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				toufangdianfenleiEchart1.setOption(option);
				//根据窗口的大小变动图表
				toufangdianfenleiEchart1.resize();
			})
		})
	}
	const getlajitoufangdianChart2 = () => {
		nextTick(()=>{
			var rongliangEchart2 = echarts.init(document.getElementById("lajitoufangdianrongliangEchart2"),'macarons');
			context?.$http({
				url: `lajitoufangdian/value/toufangdianmingcheng/rongliang`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].toufangdianmingcheng);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].toufangdianmingcheng
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '各垃圾投放点容量统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				// 使用刚指定的配置项和数据显示图表。
				rongliangEchart2.clear()
				rongliangEchart2.setOption(option);
				//根据窗口的大小变动图表
				rongliangEchart2.resize();
			})
		})
	}
	const getlajiyunshucheliangChart1 = () => {
		nextTick(()=>{
			var cheliangleixingEchart1 = echarts.init(document.getElementById("lajiyunshucheliangcheliangleixingEchart1"),'macarons');
			context?.$http({
				url: "lajiyunshucheliang/group/cheliangleixing",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].cheliangleixing);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].cheliangleixing
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '车辆类型数据统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				cheliangleixingEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				cheliangleixingEchart1.setOption(option);
				//根据窗口的大小变动图表
				cheliangleixingEchart1.resize();
			})
		})
	}
	init()
</script>
<style lang="scss">
	.projectTitle{
		padding: 30px 0 20px;
		margin: 20px 0 20px;
		color: #333;
		font-weight: bold;
		display: flex;
		width: 100%;
		font-size: 30px;
		justify-content: center;
		align-items: center;
		height: auto;
	}

	.showIcons {
		transition: transform 0.3s;
		margin-right: 10px;
	}

	.showIcons1 {
		transform: rotate(-180deg);
	}
	
	// 总数盒子
	.count_list{
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 总数card
		.card_view {
			border: 0px solid #fccb8250;
			border-radius: 0;
			box-shadow: 0px 0px 0px rgba(0,0,0,.12);
			margin: 0 1% 20px;
			background: none;
			width: 23%;
			box-sizing: border-box;
			height: auto;
			// card头部
			.el-card__header {
				border: 0px solid #7da066;
				background: #7da06620;
				width: 100%;
				border-width: 0 0 0px;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					height: 10px;
					// 标题
					.card_head_title {
						color: #333;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon {
							cursor: pointer;
							color: #333;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				border-radius: 0 0 20px 20px;
				padding: 0;
				background: #fff;
				border-color: #7da06630;
				border-width: 0 1px 1px;
				border-style: solid;
				// body盒子
				.count_item{
					padding: 30px;
					flex-direction: column;
					display: flex;
					justify-content: center;
					align-items: center;
					text-align: center;
					// 总数标题
					.count_title{
						margin: 0 0px 0 0;
						color: #666;
						font-weight: 500;
						display: inline-block;
						font-size: 16px;
						line-height: 1.5;
					}
					// 总数数字
					.count_num{
						color: #c00;
						font-weight: 600;
						display: inline-block;
						font-size: 26px;
						line-height: 1.5;
					}
				}
			}
		}
	}
	// 首页盒子
	.home_view {
		padding: 0px 20px;
		margin: 0;
		background: url(http://clfile.zggen.cn/20240417/41207a459fdc41d182fa01f86aa9056c.png) no-repeat center top / 100% 100% !important;
		width: 100%;
		min-height: 100vh;
		height: auto;
	}
	// 统计图盒子
	.card_list {
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: space-between;
		align-items: flex-start;
		flex-wrap: wrap;
		// 统计图card
		.card_view {
			border: 0px solid #fccb8250;
			border-radius: 0;
			box-shadow: 0px 0px 0px rgba(0,0,0,.12);
			margin: 10px;
			background: none;
			width: calc(50% - 20px);
			box-sizing: border-box;
			height: auto;
			// 头部
			.el-card__header {
				border: 0px solid #eee;
				background: #7da06620;
				width: 100%;
				border-width: 0 0 0px;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					height: 10px;
					// 标题
					.card_head_title {
						color: #333;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon{
							cursor: pointer;
							color: #333;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				border-radius: 0 0 20px 20px;
				padding: 0;
				background: #fff;
				border-color: #7da06630;
				border-width: 0 1px 1px;
				border-style: solid;
				// body盒子
				.card_item{
					padding: 10px;
					text-align: center;
				}
			}
		}
	}
</style>
